import { Component, OnInit } from '@angular/core';
import { StorageService } from '../storage.service';


@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.scss']
})
export class SettingsComponent implements OnInit {
  public activeTab = 0;
  public categories: any = [];
  private storage: any;

  constructor(
    private storageService: StorageService,
  ) { }

  ngOnInit(): void {
    this.categories = this.storageService.getStorage();
    this.categories.forEach(element => {
      element.collapsed = true;
    });
  }


  public add(): void {
    this.categories.unshift(this.generateCategory());
  }

  public categoryRemoved(category): void {
    const index: number = this.categories.findIndex((cat) => cat.id === category.id && cat.name === category.name);
    this.categories.splice(index, 1);
    this.storageService.updateCategoriesStorage(this.categories);
  }

  public changePassword(): any {
    const oldPassword: string | undefined = this.storageService.getPassword();
    if (oldPassword) {
      const confPassword = prompt('Введите ваш текущий пароль');
      if (confPassword !== null) {
        if (confPassword === oldPassword) {
          this.updatePassword();
        } else {
          alert('Вы ввели не верный пароль!');
        }
      }
    } else {
      this.updatePassword();
    }
  }

  private updatePassword(): void {
    const password1 = prompt('Введите новый пароль');
    if (password1) {
      const password2 = prompt('Повторите введенный пароль');
      if (password2) {
        if (password1 === password2) {
          this.storageService.updatePassword(password1);
        } else {
          alert('Пароли не совпадают! Попробуйте снова');
        }
      }
    }
  }

  private generateCategory(): any {
    let id = 1;
    if (this.categories.length) {
      const ids = this.categories.map((category: any) => category.id);
      id = Math.max(...ids) + 1;
    }
    return {
      id,
      index: 1,
      name: '',
      items: []
    };
  }
}
