import { Component, OnInit } from '@angular/core';
import { StorageService } from '../storage.service';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss']
})
export class MainComponent implements OnInit {
  public categories: any = [];
  public activeTab = 0;
  public order: any = [];
  public total = 0;
  public payModal = false;
  constructor(
    private storageService: StorageService,
  ) { }

  ngOnInit(): void {
    this.categories = this.storageService.getStorage().sort((a, b) => a.index - b.index);
  }

  public addToOrder(product): void {
    const isInTheOrder = this.order.find((inOrder) => inOrder.name === product.name && inOrder.price === product.price);
    if (isInTheOrder) {
      this.increase(isInTheOrder);
    } else {
      product.amount = 1;
      this.order.push(product);
    }
    this.calculateTotal();
  }

  public decrease(product): void {
    product.amount -= 1;
    this.calculateTotal();
  }

  public increase(product): void  {
    product.amount += 1;
    this.calculateTotal();
  }

  public remove(product): void {
    const index = this.order.findIndex((inOrder) => inOrder.name === product.name && inOrder.price === product.price);
    this.order.splice(index, 1);
    this.calculateTotal();
  }

  public clear(): any {
    this.order = [];
    this.total = 0;
    return false;
  }

  public pay(): any {
    if (this.total) {
      this.payModal = true;
      return false;
    } else {
      return false;
    }
  }

  public payCard(): any {
    this.payment('card');
    return false;
  }

  public payCash(): any {
    const result = prompt('Сколько дал клиент?');

    if (typeof result === 'string' && result === '') {
      this.payment('cash');
      return false;
    }

    if (!Number.isInteger(+result)) {
      alert('Вы ввели не число!');
      return;
    } else {
      const total = +result - this.total;
      if (total < 0) {
        alert('Число меньше 0! Что то не так!');
      } else {
        alert(`Cдача ${total} грн.`);
        this.payment('cash');
      }
    }
    return false;
  }

  private payment(type: string): void {
    alert(`Заказ успешно завершен на сумму ${this.total}`);
    const time = new Date().toLocaleTimeString('default', {
      hour: '2-digit',
      minute: '2-digit',
    });
    this.order.forEach((item) => {
      item.payment = type;
      item.time = `${this.getDate()}, ${time}`;
    });
    this.storageService.saveOrder(this.order);
    this.clear();
    this.payModal = false;
  }

  private getDate(): string {
    const dateObj = new Date();
    const month = dateObj.getUTCMonth() + 1;
    const day = dateObj.getUTCDate();
    const year = dateObj.getUTCFullYear();

    return year + '/' + month + '/' + day;
  }

  private calculateTotal(): void {
    this.total = this.order.reduce((sum, current) => {
      return sum + (current.price * current.amount);
    }, 0);
  }
}
